import {ParametricIcon} from "/node_modules/parametric-icon/index.js"
import {template} from "../templates/Stars5.js"

/**
 * @class Stars5
 * @extends ParametricIcon
 * @classdesc 
 * 
 * A parameterizable icon, use is with the icon-stars5 html tag. 
 * All the parameter of this class can also be passed through html attribute.
 * 
 * @property {string} grade an image parameter.
 */
class Stars5 extends ParametricIcon {

	connectedCallback(){
		this.setStyle(":host{display:block}")
		super.connectedCallback()
	}
	static get shadowMode(){
		return "open"
	}
	static get imageParameters() {
		return ["grade"]
	}
	static get serialize() {
		return this.standardSerial
	}
	static get template(){
		return template
	}
}
export default Stars5
