import {ParametricIcon} from "/node_modules/parametric-icon/index.js"
import {template} from "../templates/CustomNoiseIconWithHeight.js"

/**
 * @class CustomNoiseIconWithHeight
 * @extends ParametricIcon
 * @classdesc 
 * 
 * A parameterizable icon, use is with the icon-custom-noise-icon-with-height html tag. 
 * All the parameter of this class can also be passed through html attribute.
 * 
 * @property {number} rodelength How much the image is padded bellow. The height of the original image is used as unite for this value.
 * @property {string} fill an image parameter.
 * @property {string} stroke an image parameter.
 * @property {string} rodtext The text  on the rode. Visible only if offsetheight is hight enough.
 * @property {string} text an image parameter.
 * @property {number} strength strength How much of the sound effect on the front is visible, on a range from 0 to 100
 */
class CustomNoiseIconWithHeight extends ParametricIcon {

	connectedCallback(){
		this.setStyle(":host{display:block}")
		super.connectedCallback()
	}
	static get shadowMode(){
		return "open"
	}
	static get imageParameters() {
		return ["rodelength","fill","stroke","rodtext","text","strength"]
	}
	static get serialize() {
		let s =  this.standardSerial
		s.strength = "number"
		s.rodelength = "number"

		return s
	}
	static get template(){
		return template
	}
}
export default CustomNoiseIconWithHeight
