import {ParametricIcon} from "/node_modules/parametric-icon/index.js"
import {template} from "../templates/SmallGoogleResult.js"

/**
 * @class SmallGoogleResult
 * @extends ParametricIcon
 * @classdesc 
 * 
 * A parameterizable icon, use is with the icon-small-google-result html tag. 
 * All the parameter of this class can also be passed through html attribute.
 * 
 * @property {string} colorcircle an image parameter.
 * @property {string} colorglyph an image parameter.
 * @property {string} letter an image parameter.
 */
class SmallGoogleResult extends ParametricIcon {

	connectedCallback(){
		this.setStyle(":host{display:block}")
		super.connectedCallback()
	}
	static get shadowMode(){
		return "open"
	}
	static get imageParameters() {
		return ["colorcircle","colorglyph","letter"]
	}
	static get serialize() {
		return this.standardSerial
	}
	static get template(){
		return template
	}
}
export default SmallGoogleResult
