import {ParametricIcon} from "/node_modules/parametric-icon/index.js"
import {template} from "../templates/Temperature.js"

/**
 * @class Temperature
 * @extends ParametricIcon
 * @classdesc 
 * 
 * A parameterizable icon, use is with the icon-temperature html tag. 
 * All the parameter of this class can also be passed through html attribute.
 * 
 * @property {string} fill an image parameter.
 * @property {string} stroke an image parameter.
 * @property {string} percent an image parameter.
 * @property {string} temperature an image parameter.
 */
class Temperature extends ParametricIcon {

	connectedCallback(){
		this.setStyle(":host{display:block}")
		super.connectedCallback()
	}
	static get shadowMode(){
		return "open"
	}
	static get imageParameters() {
		return ["fill","stroke","percent","temperature"]
	}
	static get serialize() {
		return this.standardSerial
	}
	static get template(){
		return template
	}
}
export default Temperature
