import {ParametricIcon} from "/node_modules/parametric-icon/index.js"
import {template} from "../templates/AirQualityCluster.js"

/**
 * @class AirQualityCluster
 * @extends ParametricIcon
 * @classdesc 
 * 
 * A parameterizable icon, use is with the icon-air-quality-cluster html tag. 
 * All the parameter of this class can also be passed through html attribute.
 * 
 * @property {string} fill an image parameter.
 * @property {string} stroke an image parameter.
 * @property {string} tickvisible an image parameter.
 * @property {string} alerts an image parameter.
 */
class AirQualityCluster extends ParametricIcon {

	connectedCallback(){
		this.setStyle(":host{display:block}")
		super.connectedCallback()
	}
	static get shadowMode(){
		return "open"
	}
	static get imageParameters() {
		return ["fill","stroke","tickvisible","alerts"]
	}
	static get serialize() {
		let std = this.standardSerial
		std.tickvisible = "boolean"
		return std
	}
	static get template(){
		return template
	}
}
export default AirQualityCluster
