
let {String,Number,Boolean} = window

export {String as string}
export {Number as number}
export function int(v){
	return Math.floor(v)
}
export {Boolean as boolean}
export function array(v) {
	return Array.from(v)
}
export function set(v) {
	return new Set(...v)
}
export function date(v) {
	return new Date(v)
}
function func(v) {
	if(typeof v !== "function")
		throw new Error("A function was expected, got : "+v)
	return v
} 
export {func as function}

