import {ParametricIcon} from "/node_modules/parametric-icon/index.js"
import {template} from "../templates/CarsFrontView.js"

/**
 * @class CarsFrontView
 * @extends ParametricIcon
 * @classdesc 
 * 
 * A parameterizable icon, use is with the icon-cars-front-view html tag. 
 * All the parameter of this class can also be passed through html attribute.
 * 
 * @property {string} fill an image parameter.
 * @property {string} stroke an image parameter.
 * @property {boolean} visibility1 Is back car visible
 * @property {boolean} visibility2 Is middle car visible
 * @property {boolean} visibility3 Is front car visible
 */
class CarsFrontView extends ParametricIcon {

	connectedCallback(){
		this.setStyle(":host{display:block}")
		super.connectedCallback()
	}
	static get shadowMode(){
		return "open"
	}
	static get imageParameters() {
		return ["fill","stroke","visibility1","visibility2","visibility3"]
	}
	static get serialize() {
		let std = this.standardSerial
		std.visibility1 = "boolean"
		std.visibility2 = "boolean"
		std.visibility3 = "boolean"
		return std
}
	static get template(){
		return template
	}
}
export default CarsFrontView
