import {ParametricIcon} from "/node_modules/parametric-icon/index.js"
import {template} from "../templates/Pm10.js"

/**
 * @class Pm10
 * @extends ParametricIcon
 * @classdesc 
 * 
 * A parameterizable icon, use is with the icon-pm10 html tag. 
 * All the parameter of this class can also be passed through html attribute.
 * 
 * @property {number} offsetheight Amount of transparent padding bellow the image. Non padded image height used as unit.
 * @property {string} fill an image parameter.
 * @property {string} stroke an image parameter.
 * @property {string} desc an image parameter.
 */
class Pm10 extends ParametricIcon {

	connectedCallback(){
		this.setStyle(":host{display:block}")
		super.connectedCallback()
	}
	static get shadowMode(){
		return "open"
	}
	static get imageParameters() {
		return ["offsetheight","fill","stroke","desc"]
	}
	static get serialize() {
		let s = this.standardSerial
		s.offsetheight = "number"
		return s
	}
	static get template(){
		return template
	}
}
export default Pm10
