import {ParametricIcon} from "/node_modules/parametric-icon/index.js"
import {template} from "../templates/ParkWithCounter.js"

/**
 * @class ParkWithCounter
 * @extends ParametricIcon
 * @classdesc 
 * 
 * A parameterizable icon, use is with the icon-park-with-counter html tag. 
 * All the parameter of this class can also be passed through html attribute.
 * 
 * @property {string} color an image parameter.
 * @property {string} free an image parameter.
 * @property {string} totalslots an image parameter.
 */
class ParkWithCounter extends ParametricIcon {

	connectedCallback(){
		this.setStyle(":host{display:block}")
		super.connectedCallback()
	}
	static get shadowMode(){
		return "open"
	}
	static get imageParameters() {
		return ["color","free","totalslots"]
	}
	static get serialize() {
		return this.standardSerial
	}
	static get template(){
		return template
	}
}
export default ParkWithCounter
