import {ParametricIcon} from "/node_modules/parametric-icon/index.js"
import {template} from "../templates/TrafficFlow.js"

/**
 * @class TrafficFlow
 * @extends ParametricIcon
 * @classdesc 
 * 
 * A parameterizable icon, use is with the icon-traffic-flow html tag. 
 * All the parameter of this class can also be passed through html attribute.
 * 
 * 
 * @property {string} fill an image parameter.
 * @property {string} stroke an image parameter.
 * @property {number} carcount The number of car displayed (0 to 4)
 * @property {boolean} qm_display If true show a question mark on top of the icon.
 */
class TrafficFlow extends ParametricIcon {

	connectedCallback(){
		this.setStyle(":host{display:block}")
		super.connectedCallback()
	}
	static get shadowMode(){
		return "open"
	}
	static get imageParameters() {
		return ["fill","stroke","carcount","qm_display"]
	}
	static get serialize() {
		let s=  this.standardSerial
		s.carcount = "number"
		s.qm_display = "boolean"
		return s
	}
	static get template(){
		return template
	}
}
export default TrafficFlow
