import {ParametricIcon} from "/node_modules/parametric-icon/index.js"
import {template} from "../templates/Water.js"

/**
 * @class Water
 * @extends ParametricIcon
 * @classdesc 
 * 
 * A parameterizable icon, use is with the icon-water html tag. 
 * All the parameter of this class can also be passed through html attribute.
 * 
 * @property {string} fullpercent an image parameter.
 * @property {string} pannelcolor an image parameter.
 */
class Water extends ParametricIcon {

	connectedCallback(){
		this.setStyle(":host{display:block}")
		super.connectedCallback()
	}
	static get shadowMode(){
		return "open"
	}
	static get imageParameters() {
		return ["fullpercent","pannelcolor"]
	}
	static get serialize() {
		return this.standardSerial
	}
	static get template(){
		return template
	}
}
export default Water
